      font-size:36px;
    }
    header>#btn{
      display:flex;
      top:25px;
    }
  nav{
    position:fixed;
    /* left:0;  */
    left:-480px;
    top:0;
    width:400px;
    height:100%;
    padding:36px;
    box-sizing:border-box;
    background-color:rgba(0,0,0,0.8);
