/* nav */
nav{
  position:fixed;
      left:-100%;
      /* left:0; */
      top:0;
      width:100%;
      height:100%;
      background-color:rgba(0,0,0,0.8);
      z-index:999;
      padding:12.5vh;
  box-sizing:border-box;
  }
  nav>.close{
    position:absolute;
    width:25px;
    height:24px;
    right:10px;
    top:10px;
    background-color:red;
  }
    nav>ul{
      width:100%; height:100%;
      display:flex;
      justify-content: center;
      align-items:center;
      flex-direction: column;
    }
  nav>ul>li{
      display:block;
      width:100%;
      /* 문제가 없어서 통과 */
      margin-left:0;
     border-bottom:1px solid #fff;
     box-sizing:border-box;
    }
  nav>ul>li:first-child{
    border-top: 1px solid #fff;
  }
  nav>ul>li>a{
      font-size:24px;
      line-height:12.5vh;
    }
  header>#btn{
    display:block;}
